.experience-section {
  background: #232127;
  padding: 48px 0 36px 0;
  position: relative;
  overflow: hidden;
}

.experience-section::before {
  content: '';
  position: absolute;
  inset: 0;
  background-image: linear-gradient(90deg, rgba(255,255,255,0.04) 1px, transparent 1px),
    linear-gradient(180deg, rgba(255,255,255,0.04) 1px, transparent 1px);
  background-size: 40px 40px;
  pointer-events: none;
  z-index: 0;
}

.exp-icon {
  margin-bottom: 0.5rem;
  z-index: 1;
  position: relative;
}

.exp-value {
  font-size: 2.8rem;
  font-family: 'Fira Mono', 'Consolas', monospace;
  font-weight: bold;
  color: #fff;
  letter-spacing: 2px;
  z-index: 1;
  position: relative;
}

.exp-label {
  color: #bdbdbd;
  font-size: 1.1rem;
  font-family: 'Fira Mono', 'Consolas', monospace;
  margin-top: 0.2rem;
  z-index: 1;
  position: relative;
}

@media (max-width: 991px) {
  .exp-value {
    font-size: 2rem;
  }
  .experience-section {
    padding: 32px 0 24px 0;
  }
} 