.navbar {
  position: sticky;
  top: 0;
  z-index: 1050;
  background: #fff;
  padding: 1rem 2rem;
  box-shadow: 0 4px 16px rgba(0,0,0,0.12);
}

.navbar-list {
  list-style: none;
  display: flex;
  gap: 2rem;
  margin: 0;
  padding: 0;
}

.navbar-list li a {
  text-decoration: none;
  color: #333;
  font-weight: 500;
  transition: color 0.2s;
}

.navbar-list li a:hover {
  color: #007bff;
} 