:root {
  --bs-primary:   #FF6B35;
  --bs-secondary: #8B9DC3;
  --bs-success:   #4CAF50;
  --bs-danger:    #E63946;
  --bs-warning:   #FFD166;
  --bs-info:      #38BDF8;
  --bs-light:     #F8F9FA;
  --bs-dark:      #22223B;

  /* Primary states */
  --bs-primary-hover: #e65c2a;
  --bs-primary-active: #cc5225;
  --bs-primary-focus: #ff8a5c;

  /* Secondary states */
  --bs-secondary-hover: #6c7ba1;
  --bs-secondary-active: #556080;
  --bs-secondary-focus: #aab8d8;

  /* Success states */
  --bs-success-hover: #388e3c;
  --bs-success-active: #256029;
  --bs-success-focus: #66bb6a;

  /* Danger states */
  --bs-danger-hover: #c82333;
  --bs-danger-active: #a71d2a;
  --bs-danger-focus: #f28487;

  /* Warning states */
  --bs-warning-hover: #e6b800;
  --bs-warning-active: #b38f00;
  --bs-warning-focus: #ffe082;

  /* Info states */
  --bs-info-hover: #0ea5e9;
  --bs-info-active: #0369a1;
  --bs-info-focus: #7dd3fc;

  /* Light and dark can use default Bootstrap states or be customized if needed */

  /* Backgrounds */
  --bs-primary-bg: #FF6B35;
  --bs-secondary-bg: #8B9DC3;
  --bs-success-bg: #4CAF50;
  --bs-danger-bg: #E63946;
  --bs-warning-bg: #FFD166;
  --bs-info-bg: #38BDF8;
  --bs-light-bg: #F8F9FA;
  --bs-dark-bg: #22223B;

  /* Borders */
  --bs-primary-border: #FF6B35;
  --bs-secondary-border: #8B9DC3;
  --bs-success-border: #4CAF50;
  --bs-danger-border: #E63946;
  --bs-warning-border: #FFD166;
  --bs-info-border: #38BDF8;
  --bs-light-border: #F8F9FA;
  --bs-dark-border: #22223B;

  /* Text */
  --bs-primary-text: #FF6B35;
  --bs-secondary-text: #8B9DC3;
  --bs-success-text: #4CAF50;
  --bs-danger-text: #E63946;
  --bs-warning-text: #FFD166;
  --bs-info-text: #38BDF8;
  --bs-light-text: #F8F9FA;
  --bs-dark-text: #22223B;

  --bs-primary-gradient: linear-gradient(90deg, #FF6B35 0%, #FF9A3C 100%);
  --bs-primary-soft: #ffb48a;
} 