/* Timeline.css */
.timeline-container {
    display: flex;
    flex-direction: column;
    position: relative;
    margin: 40px 0;
    padding: 0 20px; /* Añade un padding para evitar que los elementos toquen los bordes */
  }
  
  /* Línea central */
  .timeline-container::after {
    content: '';
    position: absolute;
    width: 4px;
    background-color: #e0e0e0; /* Línea central gris */
    top: 0;
    bottom: 0;
    left: 50%; /* Por defecto en el centro */
    transform: translateX(-50%);
    z-index: -1;
  }
  
  /* TimelineItem.css */
  .timeline-item {
    display: flex;
    justify-content: flex-end; /* Por defecto a la derecha para empujar el contenido */
    padding-right: calc(50% + 30px); /* Espacio para la línea central, círculo y un poco más */
    position: relative;
    margin-bottom: 40px;
    width: 100%;
  }
  
  .timeline-item.right {
    justify-content: flex-start; /* Mueve el contenido a la izquierda del punto central */
    padding-right: 0; /* Reinicia padding para el lado derecho */
    padding-left: calc(50% + 30px); /* Espacio para la línea central, círculo y un poco más */
  }
  
  .timeline-item-content {
    /* background-color: #fff; */
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    /* max-width: 45%;  */
    position: relative;
    border: 2px solid transparent; /* Borde transparente por defecto */
    transition: border-color 0.3s ease; /* Transición suave para el cambio de color de borde */
  }
  
  /* Flechas/colas que apuntan a la línea central */
  .timeline-item.left .timeline-item-content::after {
    content: '';
    position: absolute;
    top: 30px;
    right: -16px; /* Posiciona la punta hacia la línea */
    width: 0;
    height: 0;
    border-top: 8px solid transparent;
    border-bottom: 8px solid transparent;
    border-left: 16px solid #fff; /* Color del "triángulo" */
  }
  
  .timeline-item.right .timeline-item-content::after {
    content: '';
    position: absolute;
    top: 30px;
    left: -16px; /* Posiciona la punta hacia la línea */
    width: 0;
    height: 0;
    border-top: 8px solid transparent;
    border-bottom: 8px solid transparent;
    border-right: 16px solid #fff; /* Color del "triángulo" */
  }
  
  
  .timeline-item-content .circle {
    background-color: #6a1b9a; /* Color por defecto del círculo */
    border-radius: 50%;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width: 40px; /* Tamaño del círculo */
    height: 40px; /* Tamaño del círculo */
    z-index: 100;
    border: 4px solid #e0e0e0; /* Borde del círculo que se mezcla con la línea */
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0 0 0 4px #fff; /* Borde blanco para el círculo sobre la línea */
  }
  
  /* Posicionamiento del círculo para left y right */
  .timeline-item.left .timeline-item-content .circle {
    left: calc(100% + 20px); /* A la derecha de la tarjeta, en la línea */
  }
  
  .timeline-item.right .timeline-item-content .circle {
    right: calc(100% + 20px); /* A la izquierda de la tarjeta, en la línea */
  }
  
  
  .timeline-item-date {
    font-size: 0.9em;
    color: #FF6B35;
    display: block;
    margin-bottom: 5px;
  }
  
  .timeline-item-title {
    margin: 0 0 10px 0;
    color: #fff;
  }
  
  .timeline-item-company,
  .timeline-item-description {
    margin-bottom: 5px;
    line-height: 1.4;
    color: #fff;
  }
  
  .timeline-item-technologies {
    font-size: 0.8em;
    color: #555;
    margin-top: 10px;
    display: block;
  }
  
  /* --- Media Queries para responsividad --- */
  @media screen and (max-width: 768px) {
    .timeline-container {
      padding: 0 10px; /* Reduce padding lateral en móviles */
    }
  
    .timeline-container::after {
      left: 20px; /* Mueve la línea central a la izquierda en pantallas pequeñas */
      transform: translateX(0); /* Elimina el centrado */
    }
  
    .timeline-item {
      justify-content: flex-end; /* Todo a la derecha */
      padding-right: 0;
      padding-left: 60px; /* Espacio para la línea y círculo */
      margin-bottom: 30px;
    }
  
    .timeline-item.right {
      justify-content: flex-end; /* También a la derecha para mantener la consistencia */
      padding-left: 60px;
    }
  
    .timeline-item-content {
      max-width: 100%; /* Las tarjetas ocupan todo el ancho disponible */
      width: 100%; /* Asegura que la tarjeta se ajuste al 100% del espacio asignado */
    }
  
    /* Posicionamiento del círculo para móviles (siempre a la izquierda de la tarjeta) */
    .timeline-item-content .circle {
      left: -28px; /* Mueve el círculo a la izquierda de la tarjeta */
      right: auto; /* Desactiva la propiedad right para evitar conflictos */
    }
  
    /* Ajuste de las flechas/colas en móviles */
    .timeline-item.left .timeline-item-content::after,
    .timeline-item.right .timeline-item-content::after {
      content: '';
      position: absolute;
      top: 30px;
      left: -16px; /* La cola siempre apunta a la línea desde la izquierda */
      right: auto;
      width: 0;
      height: 0;
      border-top: 8px solid transparent;
      border-bottom: 8px solid transparent;
      border-right: 16px solid #fff; /* La punta siempre es a la derecha para apuntar a la línea */
      border-left: none; /* Elimina la flecha del otro lado */
    }
  }
  /* ... existing code ... */
@media screen and (max-width: 630px) {
    .timeline-item-content .circle {
      display: none;
    }
  }