.blog-card {
    background: #23232a;
    border-radius: 18px;
    padding: 2rem 2rem 1.5rem 2rem;
    color: #fff;
    box-shadow: 0 2px 24px 0 rgba(0, 0, 0, 0.18);
    max-width: 420px;
    margin: 0 auto;
    font-family: 'Fira Mono', 'Consolas', monospace;
    position: relative;
    cursor: pointer;
}

.blog-card-image-wrapper {
    position: relative;
    border-radius: 14px;
    overflow: hidden;
    margin-bottom: 1.5rem;
    background: #18181c;
    aspect-ratio: 16/9;
    height: 225px;
    width: 100%;
}

.blog-card-frame {
    width: 1354px;
    height: 947px;
    border: none;
    border-radius: 14px;
    background: #18181c;
    pointer-events: none;
    transform: scale(0.3);
    transform-origin: 0 0;
    position: absolute;
    top: 0;
    left: 0;
}

.blog-card-image {
    width: 100%;
    display: block;
    border-radius: 14px;
    object-fit: cover;
    background: #18181c;
}

.blog-card-badge {
    position: absolute;
    left: 1.2rem;
    bottom: 1.2rem;
    background: transparent;
    color: #fff;
    border: 2px solid #fff;
    border-radius: 8px;
    padding: 0.3rem 1.2rem;
    font-size: 1.1rem;
    font-weight: 600;
    letter-spacing: 1px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.12);
    z-index: 4;
}

.blog-card:hover .blog-card-badge {
    background: var(--bs-primary);
    color: #fff;
    border: 2px solid var(--bs-primary);
}

.blog-card-float-icon {
    position: absolute;
    right: 1.2rem;
    top: 50%;
    transform: translateY(-50%);
    background: #7fff4f;
    color: #23232a;
    border-radius: 50%;
    width: 38px;
    height: 38px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.3rem;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.12);
    border: 3px solid #23232a;
    z-index: 2;
    opacity: 0;
    pointer-events: none;
    transition: opacity 0.25s;
}

.blog-card-image-wrapper:hover .blog-card-float-icon {
    opacity: 1;
    pointer-events: auto;
}

.blog-card-meta {
    color: #bdbdbd;
    font-size: 1.1rem;
    margin-bottom: 0.7rem;
    display: flex;
    align-items: center;
    gap: 0.5rem;
    letter-spacing: 1px;
}

.blog-card-meta .dot {
    font-size: 1.5rem;
    line-height: 1;
    color: #bdbdbd;
}

.blog-card-title {
    color: #fff;
    font-size: 1.5rem;
    font-weight: bold;
    margin-bottom: 0.7rem;
    letter-spacing: 1px;
    text-align: center;
}

.blog-card-desc {
    color: #bdbdbd;
    font-size: 1.1rem;
    text-align: center;
    margin-bottom: 0;
}

.blog-card-hover-overlay {
    position: absolute;
    inset: 0;
    background: rgba(0, 0, 0, 0.7);
    opacity: 0;
    transition: opacity 0.3s;
    z-index: 5;
    display: flex;
    align-items: center;
    justify-content: center;
    pointer-events: none;
    cursor: pointer;
}

.blog-card-image-wrapper:hover .blog-card-hover-overlay {
    opacity: 1;
    pointer-events: auto;
}

.blog-card-hover-btn {
    background: #fff;
    color: #23232a;
    border: none;
    border-radius: 8px;
    padding: 0.7rem 2rem;
    font-size: 1.1rem;
    font-weight: 600;
    cursor: pointer;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.18);
    transition: background 0.2s, color 0.2s, transform 0.2s;
    z-index: 4;
}

.blog-card-image-wrapper:hover .blog-card-hover-btn {
    transform: scale(1.08);
}

.blog-card-fallback {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background: #18181c;
    color: #bdbdbd;
    gap: 1rem;
}

.blog-card-fallback p {
    margin: 0;
    font-size: 1rem;
    color: var(--bs-primary);
}