.home-landing {
  min-height: 100vh;
  background: #2d2b32;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 40px 0;
}

.hexagon-card {
  background: transparent !important;
  background-color: transparent !important;
  border: none;
  box-shadow: none;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
}

.home-photo {
  width: 320px;
  height: 360px;
  object-fit: cover;
  clip-path: polygon(25% 6%, 75% 6%, 100% 50%, 75% 94%, 25% 94%, 0% 50%);
  background: #232127;
  border-radius: 16px;
  border: 6px solid #232127;
  box-shadow: 0 4px 32px rgba(0,0,0,0.4);
}

.home-content {
  padding-left: 0;
}

.home-intro {
  color: #e06c75;
  font-family: 'Fira Mono', 'Consolas', monospace;
  font-size: 1.1rem;
  letter-spacing: 1px;
  display: block;
  margin-bottom: 0.5rem;
}

.home-title {
  font-family: 'Fira Mono', 'Consolas', monospace;
  font-size: 2.8rem;
  font-weight: bold;
  line-height: 1.1;
  margin-bottom: 1.2rem;
}

.highlight {
  color: #6fcf43;
}

.home-desc {
  color: #bdbdbd;
  font-size: 1.1rem;
  margin-bottom: 1.5rem;
}

.tech-node { color: #6cc24a; font-weight: bold; }
.tech-react { color: #61dafb; font-weight: bold; }
.tech-angular { color: #e06c75; font-weight: bold; }
.tech-laravel { color: #ff2d20; font-weight: bold; }

.home-icons {
  gap: 1.2rem;
}

.icon-box {
  margin-right: 0.7rem;
  display: flex;
  align-items: center;
  color:#fff ;
}

.more {
  color: #bdbdbd;
  font-size: 1.1rem;
  margin-left: 0.7rem;
}

.home-cv-btn {
  margin-top: 1.5rem;
  font-family: 'Fira Mono', 'Consolas', monospace;
  font-size: 1.1rem;
  font-weight: 500;
  border-radius: 8px;
  border: 2px solid var(--bs-primary) !important;
  color: var(--bs-primary) !important;
  background: transparent !important;
  padding: 0.7rem 1.5rem;
  transition: background 0.2s, color 0.2s;
  display: inline-flex;
  align-items: center;
}

.home-cv-btn:hover, .home-cv-btn:focus {
  background: var(--bs-primary-hover) !important;
  color: #fff !important;
  border-color: var(--bs-primary-hover) !important;
}

.home-cv-btn:active {
  background: var(--bs-primary-active) !important;
  color: #fff !important;
  border-color: var(--bs-primary-active) !important;
}

/* Botón primary de Reactstrap personalizado */
.btn-primary {
  background-color: var(--bs-primary) !important;
  border-color: var(--bs-primary) !important;
  color: #fff !important;
  transition: background 0.2s, border 0.2s, color 0.2s;
}
.btn-primary:hover, .btn-primary:focus {
  background-color: var(--bs-primary-hover) !important;
  border-color: var(--bs-primary-hover) !important;
  color: #fff !important;
}
.btn-primary:active {
  background-color: var(--bs-primary-active) !important;
  border-color: var(--bs-primary-active) !important;
  color: #fff !important;
}

.btn-secondary {
  background-color: var(--bs-secondary) !important;
  border-color: var(--bs-secondary) !important;
  color: #fff !important;
}
.btn-secondary:hover, .btn-secondary:focus {
  background-color: var(--bs-secondary-hover) !important;
  border-color: var(--bs-secondary-hover) !important;
  color: #fff !important;
}
.btn-secondary:active {
  background-color: var(--bs-secondary-active) !important;
  border-color: var(--bs-secondary-active) !important;
  color: #fff !important;
}

@media (max-width: 991px) {
  .home-landing {
    flex-direction: column;
    padding: 30px 0;
  }
  .home-photo {
    width: 220px;
    height: 240px;
  }
  .home-title {
    font-size: 2rem;
  }
}

/* Cards */
.card {
  background-color: var(--bs-light-bg) !important;
  border-color: var(--bs-primary-border) !important;
  color: var(--bs-dark-text) !important;
}
.card-header {
  background-color: var(--bs-primary-bg) !important;
  color: #fff !important;
  border-bottom: 1px solid var(--bs-primary-border) !important;
}
.card-body {
  background-color: var(--bs-light-bg) !important;
  color: var(--bs-dark-text) !important;
}

/* Alerts */
.alert-primary {
  background-color: var(--bs-primary-bg) !important;
  color: #fff !important;
  border-color: var(--bs-primary-border) !important;
}
.alert-secondary {
  background-color: var(--bs-secondary-bg) !important;
  color: #fff !important;
  border-color: var(--bs-secondary-border) !important;
}
/* Repite para success, danger, warning, info si los usas */

/* Badges */
.badge-primary {
  background-color: var(--bs-primary-bg) !important;
  color: #fff !important;
}
.badge-secondary {
  background-color: var(--bs-secondary-bg) !important;
  color: #fff !important;
}

/* Inputs */
.form-control:focus {
  border-color: var(--bs-primary-border) !important;
  box-shadow: 0 0 0 0.2rem rgba(255,107,53,0.25) !important;
}

/* Navbar */
.navbar {
  background-color: var(--bs-dark-bg) !important;
}
.navbar .nav-link,
.navbar .navbar-brand {
  color: var(--bs-light-text) !important;
}
.navbar .nav-link.active, .navbar .nav-link:focus, .navbar .nav-link:hover {
  color: var(--bs-primary) !important;
}

/* Links */
a {
  color: var(--bs-primary-text);
  transition: color 0.2s;
}
a:hover, a:focus {
  color: var(--bs-primary-hover);
} 
.footer{
    background-color: var(--bs-dark-bg) !important;
}